 .paginationBttns {
        display: flex;
        list-style: none;
        justify-content: center;
        color: rgb(200, 175, 240);
        
    }
    
    .paginationBttns a {
        padding: 6px 9px;
        margin: 8px;
        border-radius: 50%;
        text-decoration: none;
        font-size: 25px;
        cursor: pointer;
        background :goldenrod ;
        color: darkgreen;  
      }
    
    .paginationBttns a:hover {
        background: white;
        border: 1px solid green;
    }
    
    .paginationActive a {
        border: 1px solid green;
        background: green;
    }